import React from 'react';
import Radio from './Form/Radio';
import questions from './data/Questions';

function App() {
  const [answers, setAnswers] = React.useState({
    p1: '',
    p2: '',
    p3: '',
    p4: '',
  })

  const [slide, setSlide] = React.useState(0);
  const [result, setResult] = React.useState(null);

  const handleChange = ({ target }) => {
   setAnswers({...answers, [target.id]: target.value});
  }

  const finalResult = () => {
    const correctAnswers = questions.filter(({id, resposta}) =>  answers[id] === resposta);
    setResult(`Você acertou: ${correctAnswers.length}  ${questions.length}`);
  }

  const handleClick = () => {
    if(slide < questions.length -1) {
      setSlide(slide + 1);
    } else {
      setSlide(slide + 1);
      finalResult()
    }
  }

  return (
    <form onSubmit={(event) => event.preventDefault()}>
      {questions.map((question, index) => (
        <Radio
          active={slide === index}
          key={ question.id }
          onChange={ handleChange }
          value={answers[question.id]} 
          question = { { ...question } }
        />
      ))}
      {result ? <p>{result}</p> : <button onClick={handleClick}>Next</button>}
    </form>
    
  );
}

export default App;
