import React from 'react'
import './radio.css';

const Radio = ({question, onChange, value, active}) => {
  if (active === false) return null;
  return (
   <fieldset className = "constainerFieldset">
     <legend className = "tituloLegenda">{ question.pergunta }</legend>
     {question.options.map((option) => (
       <label key={ option } className = "label" >
         <input 
          // htmlfor={ option }
          type='radio'
          value={ option }
          id={question.id}
          onChange = { onChange }
          checked={value === option}
         />
         {option}
       </label>
     ))}
   </fieldset>
  )
}

export default Radio;
